import { useState } from "react";
import { size } from "lodash";
import { getRegistroApi,getColegiadoByCopApi } from "../api/registro";
import { useAuth} from "./";

export function useRegistro() {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [registros, setRegistros] = useState(null);
  const [colegiado,setColegiado]= useState(null);

  const { auth } = useAuth();

  const getRegistros = async (page,perPage) => {
    try {
      setLoading(true);
      const response = await getRegistroApi(auth.token,page,perPage);
      setLoading(false);
      setRegistros(response);
    } catch (error) {
      setLoading(false);
      setError(error);
    }
  };
  const getColegiadoByCop=async(ncop)=>{
    try{
      setLoading(true);
      const response=await getColegiadoByCopApi(ncop);
      setLoading(false);
      setColegiado(response);
    }catch(error){
    setLoading(false);
    setError(error);
    }
  }
  return {
    loading,
    error,
    registros,
    getRegistros,
    colegiado,
    getColegiadoByCop,
  };
}
